import axios from "axios";
import globals from "../globals";

export default function (squadro, prov) {
  let url = "/geoservices/REST/sigmater/fogli_squadro/" + squadro;
  if (prov) {
    url += "/" + prov
  }
  return axios.get(url).then(response => response.data.data);
}



